import React from 'react'
import { useSelector } from 'react-redux';
import { Stage, Layer, Rect, Text, Circle, Line, Group } from 'react-konva';



const TreeLevelCircleLineComponent = () => {
    const konvaInfo = useSelector( state => state.KonvaData )
    console.log( 'konvaInfo.points.y1', konvaInfo.points.y1 )
    console.log( 'konvaInfo.movableNodesPoints.y', konvaInfo.movableNodesPoints.y )


    return (
        <>
            <Group>
                <Line
                    points={ [ konvaInfo.points.x1, konvaInfo.points.y1, konvaInfo.points.x2, konvaInfo.points.y2 ] }
                    stroke="black"
                />
                <Circle x={ konvaInfo.points.x1 } y={ konvaInfo.points.y1 } radius={ konvaInfo.circleRadius } fill={ konvaInfo.circleFill } />
                <Circle x={ konvaInfo.points.x2 } y={ konvaInfo.points.y2 } radius={ konvaInfo.circleRadius } fill={ konvaInfo.circleFill } />
                {/* <Circle x={ konvaInfo.points.x1 } y={ konvaInfo.points.y1 } radius={ konvaInfo.circleRadiusOne } fill={ konvaInfo.circleFillOne } />
                <Circle x={ konvaInfo.points.x2 } y={ konvaInfo.points.y2 } radius={ konvaInfo.circleRadiusOne } fill={ konvaInfo.circleFillOne } /> */}
                {/* state.movableNodesPoints.x = ( state.points.x2 + ( 2 * state.points.x1 ) ) / 3 */ }
                <Circle x={ ( konvaInfo.points.x2 + ( 2 * konvaInfo.points.x1 ) ) / 3 }
                    y={ konvaInfo.movableNodesPoints.y === undefined ? 500 : konvaInfo.movableNodesPoints.y }
                    radius={ konvaInfo.circleRadius } fill={ konvaInfo.circleFill } />
                {/* <Circle x={ ( konvaInfo.points.x2 + konvaInfo.points.x1 ) / 2 } y={ konvaInfo.points.y2 } radius={ konvaInfo.circleRadiusOne } fill={ konvaInfo.circleFillOne } /> */ }
            </Group>
        </>
    )
}

export default ( TreeLevelCircleLineComponent );
