import { createSlice } from '@reduxjs/toolkit'

const KonvaData = createSlice( {
    name: 'konvaInfo',
    initialState: {
        text: "Some text on canvas",
        circleX: 200,
        circleY: 100,
        circleRadius: 10,
        circleRadiusOne: 5,
        circleFill: "black",
        circleFillOne: "white",
        lineX: 800,
        lineY: 500,
        // { [ 500, 500, 900, 500 ] }
        points: {
            x1: 500,
            y1: 500,
            x2: 1200,
            y2: 500
        },
        movableNodesPoints: {
            x: undefined,
            y: undefined,
        },
        linePoints: [ 0, 0, 100, 0, 100, 100 ],
        lineTension: 0.5,
        lineClosed: "closed",
        lineStroke: "black",
        lineFillLinearGradientStartPoint: { x: -50, y: -50 },
        lineFillLinearGradientEndPoint: { x: 50, y: 50 },
        lineFillLinearGradientColorStops:
            [ 0, 'red', 1, 'yellow' ],
        rectX: 20,
        rectY: 50,
        rectWidth: 100,
        rectHeight: 100,
        rectFill: "red",
        rectShadowBlur: 10,
        distanceFromNodeA: 200,
        distanceFromNodeB: 500
    },
    reducers: {
        setNodeOneXYPosition: ( state, action ) => {
            state.points.x1 = state.points.x1 + action.payload
            // state.points.y1 = state.points.y1 + action.payload
            // state.movableNodesPoints.y = state.points.y1-
        },
        setNodeTwoXYPosition: ( state, action ) => {
            state.points.x2 = state.points.x2 + action.payload
            // state.points.y2 = state.points.y2 + action.payload
        },
    },
} );



export const { setNodeOneXYPosition, setNodeTwoXYPosition } = KonvaData.actions;
export const getText = ( state ) => state.konvaInfo.text;
export const getCircleX = ( state ) => state.konvaInfo.circleX;
export const getCircleY = ( state ) => state.konvaInfo.circleY;
export const getCircleRadius = ( state ) => state.konvaInfo.circleRadius;
export const getCircleFill = ( state ) => state.konvaInfo.circleFill;
export const getLineX = ( state ) => state.konvaInfo.lineX;
export const getLineY = ( state ) => state.konvaInfo.lineY;
// export const getLinePoints = ( state ) => state.konvaInfo.linePoints;
export const getLineTension = ( state ) => state.konvaInfo.lineTension;
export const getLineClosed = ( state ) => state.konvaInfo.lineClosed;
export const getLineStroke = ( state ) => state.konvaInfo.lineStroke;
export const getLineFillLinearGradientStartPoint = ( state ) => state.konvaInfo.lineFillLinearGradientStartPoint;
export const getLineFillLinearGradientEndPoint = ( state ) => state.konvaInfo.lineFillLinearGradientEndPoint;
export const getLineFillLinearGradientColorStops = ( state ) => state.konvaInfo.lineFillLinearGradientColorStops;
export const getRectX = ( state ) => state.konvaInfo.rectX;
export const getRectY = ( state ) => state.konvaInfo.rectY;
export const getRectWidth = ( state ) => state.konvaInfo.rectWidth;
export const getRectHeight = ( state ) => state.konvaInfo.rectHeight;
export const getLinePoints = ( state ) => state.konvaInfo.points
export const getMovableNodesPoints = ( state ) => state.konvaInfo.movableNodesPoints
export const getDistanceFromNodeA = ( state ) => state.konvaInfo.distanceFromNodeA
export const getDistanceFromNodeB = ( state ) => state.konvaInfo.distanceFromNodeB

export default KonvaData.reducer
