import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { setNodeOneXYPosition, setNodeTwoXYPosition } from '../features/konvaReducer'


const ControlComponent = () => {
    // const [ yPos, setYPos ] = useState( 1 )
    const dispatch = useDispatch()
    const konvaInfo = useSelector( state => state.KonvaData )
    return (
        <>
            <button
                onClick={ ( e ) => {
                    dispatch( setNodeOneXYPosition( 5 ) )
                } }
            >Node 1</button>
            {/* <button>Movable Hole</button> */ }
            <button
                onClick={ ( e ) => {
                    dispatch( setNodeTwoXYPosition( 5 ) )
                } }
            >Node 2</button>
            {/* <button

            >NodeA distance ={ konvaInfo.distanceFromNodeA }</button>
            <button

            >NodeB distance = { konvaInfo.distanceFromNodeB }</button> */}
        </>
    )
}

export default ControlComponent
